@full_screen_height: 100vh;


.body {
  min-height: @full_screen_height;
  margin: 0;
  font-weight: 100;
  background: radial-gradient(#3bf5e5,#458bf5);
  padding: 16px 0px;
}

.glass_morphism {
  /* From https://css.glass */
  padding: 10px 30px;
  background: rgba(255, 255, 255, 0.19);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(6.4px);
  -webkit-backdrop-filter: blur(6.4px);
}
